<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>用户订单</span>
      </div>
      <!-- 头部区域 -->

      <div class="search-box">
        <div class="type">
          <el-radio-group v-model="radio1">
            <el-badge :value="NotCount" class="item">
              <el-radio-button label="未完成订单"></el-radio-button>
            </el-badge>
            <el-badge class="item">
              <el-radio-button
                label="已完成订单"
                style="margin-left: 20px"
              ></el-radio-button>
            </el-badge>
          </el-radio-group>
        </div>
      </div>
      <!-- 店铺表格区域 -->
      <el-table :data="tableData" style="width: 100%" border stripe>
        <!-- 订单编号 -->
        <el-table-column
          label="订单编号"
          align="center"
          prop="orderid"
          width="150"
        >
        </el-table-column>
        <!-- 订单菜品 -->
        <el-table-column label="订单菜品" prop="dis" align="center" width="600">
          <template v-slot="{ row }">
            <!-- 菜品详情 -->
            <el-popover placement="bottom" title="订单详情" trigger="click">
              <!-- 以下是订单的菜品详情 -->
              <template>
                <el-table :data="row.dis" style="width: 100%">
                  <!-- 菜品ID -->
                  <el-table-column
                    prop="did"
                    label="菜品ID"
                    width="100"
                    align="center"
                  >
                  </el-table-column>
                  <!-- 菜品名称 -->
                  <el-table-column
                    prop="dname"
                    label="菜品名称"
                    width="180"
                    align="center"
                  >
                  </el-table-column>
                  <!-- 菜品价格 -->
                  <el-table-column
                    prop="dprice"
                    label="菜品价格"
                    width="180"
                    align="center"
                  >
                  </el-table-column>
                  <!-- 菜品数量 -->
                  <el-table-column prop="count" label="菜品数量" align="center">
                  </el-table-column>
                  <!-- 小计 -->
                  <el-table-column prop="payment" label="小计" align="center">
                  </el-table-column>
                </el-table>
              </template>
              <!-- 以下是订单概要 -->
              <div
                title="点击显示订单详情"
                slot="reference"
                class="name-wrapper"
                style="cursor: pointer"
              >
                <span v-for="food in row.dis" :key="food.dis">
                  {{ food.dname }} x{{ food.count }}
                </span>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <!-- 支付金额 -->
        <el-table-column label="支付金额" align="center" width="100">
          <template v-slot="{ row }"> {{ row.count }}.00 </template>
        </el-table-column>
        <!-- 下单时间 -->
        <el-table-column
          label="下单时间"
          prop="date"
          align="center"
          width="170"
        >
        </el-table-column>
        <!-- 订单状态 -->
        <el-table-column label="订单状态" align="center">
          <template v-slot="{ row }">
            <el-button
              v-if="row.status == 0"
              disabled
              type="success"
              size="mini"
            >
              已完成
            </el-button>
            <el-button
              v-else-if="row.status == 1"
              disabled
              type="primary"
              size="mini"
            >
              出餐中
            </el-button>
            <el-button v-else type="info" size="mini" disabled>
              未完成
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页区域 -->
      <el-pagination
        @current-change="handleCurrentChangeFn"
        :current-page.sync="search.pagenum"
        :page-size.sync="search.pagesize"
        layout="total, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import { getUserOrderListAPI } from "@/api";
export default {
  name: "order",
  //   ==============================================以下是data==============================================
  data() {
    return {
      radio1: "未完成订单",
      // 搜索参数对象
      search: {
        pagenum: 1,
        pagesize: 7,
        isdone: 0,
      },
      // 未完成订单条数
      NotCount: 0,
      // 返回的数据总数
      total: 0,
      // 列表展示数据
      tableData: [],
      // // 已完成订单列表
      // finishList: [
      //   {
      //     orderID: "2023090001",
      //     foodList: [
      //       { foodID: 1, foodName: "小炒肉", foodPrice: 12, foodNum: 1 },
      //       { foodID: 2, foodName: "中炒肉", foodPrice: 22, foodNum: 1 },
      //       { foodID: 3, foodName: "大炒肉", foodPrice: 32, foodNum: 1 },
      //     ],
      //     orderTime: "2023-04-09 12:00",
      //     state: "已完成",
      //   },
      //   {
      //     orderID: "2023090001",
      //     foodList: [
      //       { foodID: 1, foodName: "小炒肉", foodPrice: 12, foodNum: 1 },
      //       { foodID: 2, foodName: "中炒肉", foodPrice: 22, foodNum: 1 },
      //       { foodID: 3, foodName: "大炒肉", foodPrice: 32, foodNum: 1 },
      //     ],
      //     orderTime: "2023-04-09 12:00",
      //     state: "已完成",
      //   },
      //   {
      //     orderID: "2023090001",
      //     foodList: [
      //       { foodID: 1, foodName: "小炒肉", foodPrice: 12, foodNum: 1 },
      //       { foodID: 2, foodName: "中炒肉", foodPrice: 22, foodNum: 1 },
      //       { foodID: 3, foodName: "大炒肉", foodPrice: 32, foodNum: 1 },
      //     ],
      //     orderTime: "2023-04-09 12:00",
      //     state: "已完成",
      //   },
      //   {
      //     orderID: "2023090001",
      //     foodList: [
      //       { foodID: 1, foodName: "小炒肉", foodPrice: 12, foodNum: 1 },
      //       { foodID: 2, foodName: "中炒肉", foodPrice: 22, foodNum: 1 },
      //       { foodID: 3, foodName: "大炒肉", foodPrice: 32, foodNum: 1 },
      //     ],
      //     orderTime: "2023-04-09 12:00",
      //     state: "已完成",
      //   },
      //   {
      //     orderID: "2023090001",
      //     foodList: [
      //       { foodID: 1, foodName: "小炒肉", foodPrice: 12, foodNum: 1 },
      //       { foodID: 2, foodName: "中炒肉", foodPrice: 22, foodNum: 1 },
      //       { foodID: 3, foodName: "大炒肉", foodPrice: 32, foodNum: 1 },
      //     ],
      //     orderTime: "2023-04-09 12:00",
      //     state: "已完成",
      //   },
      //   {
      //     orderID: "2023090001",
      //     foodList: [
      //       { foodID: 1, foodName: "小炒肉", foodPrice: 12, foodNum: 1 },
      //       { foodID: 2, foodName: "中炒肉", foodPrice: 22, foodNum: 1 },
      //       { foodID: 3, foodName: "大炒肉", foodPrice: 32, foodNum: 1 },
      //     ],
      //     orderTime: "2023-04-09 12:00",
      //     state: "已完成",
      //   },
      // ],
    };
  },
  //   ==============================================以下是method方法==============================================
  methods: {
    // 初始化列表
    async initOrderListFn() {
      const { data: res } = await getUserOrderListAPI(this.search);
      if (res.code != 0) return this.$message.error("获取订单列表失败！");
      this.tableData = res.data;
      this.total = res.count;
      if (this.search.isdone == 0) {
        this.NotCount = res.count;
      }
    },

    // 页码值发生了变化
    handleCurrentChangeFn(newPage) {
      // 为页码值赋值
      this.search.pagenum = newPage;
      // 重新发起请求
      this.initOrderListFn();
    },
    // 删除
    async removeFn(id) {
      // 1. 询问用户是否要删除
      const confirmResult = await this.$confirm(
        "此操作将永久删除该文件, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);

      // 2. 取消了删除
      if (confirmResult === "cancel") return;

      // 执行删除的操作
      const { data: res } = await deleteArticleAPI(id);

      if (res.code !== 0) return this.$message.error("删除失败!");
      this.$message.success("删除成功!");
      // 刷新列表数据
      this.resetListFn();
    },
  },
  //   ==============================================以下是mounted方法==============================================
  mounted() {
    this.initOrderListFn();
  },
  watch: {
    radio1: {
      immediate: true,
      handler(Value) {
        if (Value == "已完成订单") {
          this.search.isdone = 1;
        } else {
          this.search.isdone = 0;
        }
        this.pagenum = 1;
        this.initOrderListFn();
      },
    },
  },
};
</script>

<style lang="less" scoped>
.search-box {
  width: 100%;
  height: 50px;
}

// 分页器样式
.el-pagination {
  margin-top: 15px;
  text-align: center;
}

.search {
  float: left;
}
.type {
  float: right;
}
</style>
